"use client";
import React, { useState } from "react";

const leftnav = () => {
  const navData = ["My Courses", "Lessons", "Messages", "Rewards", "Settings"];
  return (
    <>
      <nav className="text-black  h-screen ">
        <div className="text-center my-10 font-extrabold text-4xl text-blue-700">
          SkillShare
        </div>
        <ul className="text-center my-10 space-y-4">
          {/* <li>My Courses</li>
                <li>Lessons</li>
                <li>Messages</li>
                <li>Rewards</li>
                <li>Settings</li> */}
          {navData.map((items, index) => (
            <li
              key={index}
              className= " text-lg mx-4 hover:bg-blue-200 hover:text-blue-500 py-2 rounded-lg cursor-pointer"
            >
              {items}
            </li>
          ))}
        </ul>
      </nav>
    </>
  );
};

export default leftnav;
